@media screen and (max-width: 390px) {
  .products-card {
    grid-column: span 4;
  }
  .product-paragraph {
    grid-column: span 6;
    font-size: 1em;
  }
  .menu-cell {
    grid-column: span 3;
    width: 125px;
    height: 62px;
  }

  .welcome img {
    height: 40px;
    width: 40px;
  }
  .welcome h1 {
    font-size: 1.5em;
  }
  #contact-info {
    grid-column: 1 / span 12;
    position: relative;
  }
  form input,
  #message {
    grid-column: 1 / span 12;
    margin-top: 10px;
  }
  form button {
    grid-column: 5 / span 4;
  }
  .products-card a {
    position: absolute;
    top: 200px;
  }
  .slider {
    width: 380px;
    height: 230px;
  }
}
